import React from 'react';

import Input from 'components/Input';
import Link from 'components/Link';
import Button from 'components/Button';

import LogoImage from 'assets/img/rioarte.png';

import {
  Container,
  Content,
  Logo,
  FormElem,
  Background,
  ContainerTitle,
  Title,
  SubTitle,
  FormActions,
} from '../component/styles';

const RecoverPassword: React.FC = () => {
  return (
    <Container>
      <Content>
        <div>
          <Logo src={LogoImage} alt="RioArte" />

          <ContainerTitle>
            <Title>Esqueceu sua senha?</Title>
            <SubTitle>Informe seu e-mail cadastrado no sistema</SubTitle>
          </ContainerTitle>

          <FormElem>
            <Input
              id="email"
              name="email"
              label="E-mail"
              appearance="outline"
              placeholder="Digite seu email"
            />

            <FormActions>
              <Link href="/">Voltar para o login</Link>

              <Button text="Lembrar senha" size="md" />
            </FormActions>
          </FormElem>
        </div>
      </Content>

      <Background />
    </Container>
  );
};

export default RecoverPassword;
